import http from '../../../../configs/httpConfig'

// ** Get all Data
export const getAllData = (params) => {
  return async (dispatch) => {
    await http.get('/quiz/list', { params }).then((response) => {
      dispatch({
        type: 'GET_ALL_DATA',
        data: response.data,
        params
      })
    })
  }
}

// ** Add new quiz
export const addQuiz = (quiz) => {
  return (dispatch) => {
    http
      .post('/quiz/add', quiz)
      .then((response) => {
        dispatch({
          type: 'QUIZ_ADD',
          data: response.data
        })
      })
      .catch((err) => console.log(err))
  }
}

export const addQuestion = (question) => {
  return (dispatch) => {
    http
      .post('/quiz/questions/add', question)
      .then((response) => {
        dispatch({
          type: 'QUIZ_QUESTION_ADD',
          data: response.data
        })
      })
      .catch((err) => console.log(err))
  }
}

export const resetQuizQuestions = () => ({
  type: 'RESET_QUESTIONS_ADDED'
})

export const deleteQuiz = (id) => {
  return (dispatch) => {
    http
      .delete(`/quiz/delete/${id}`)
      .then(() => {
        dispatch(getAllData())
      })
      .catch((err) => console.log(err))
  }
}

export const getQuizQuestions = (id) => {
  return (dispatch) => {
    dispatch({
      type: 'QUIZ_QUESTIONS_INIT'
    })
    http
      .get(`/quiz/questions/list/${id}`)
      .then((response) => {
        dispatch({
          type: 'QUIZ_QUESTIONS_DATA',
          data: response.data.data
        })
      })
      .catch((err) => console.log(err))
  }
}

export const resetQuizEditQuestions = () => ({
  type: 'RESET_QUIZ_QUESTIONS'
})

export const quizEditQuestions = (id, quizData) => {
  return (dispatch) => {
    http
      .put(`/quiz/edit/${id}`, quizData)
      .then((response) => {
        dispatch({
          type: 'QUIZ_EDIT',
          data: response.data
        })
      })
      .catch((err) => console.log(err))
  }
}

export const quizEditQuestionsEach = (id, quizData) => {
  return (dispatch) => {
    http
      .put(`/quiz/questions/edit/${id}`, quizData)
      .then((response) => {
        dispatch({
          type: 'QUIZ_EDIT_QUESTIONS',
          data: response.data
        })
      })
      .catch((err) => console.log(err))
  }
}
