// ** React Imports

// ** Custom Components

// ** Store & Actions

// ** Third Party Components
import { Badge } from 'reactstrap'
import { Edit, Trash2 } from 'react-feather'

// ** Renders Client Columns

const statusObj = {
  CLOSE: 'light-warning',
  OPEN: 'light-success'
}

export const columns = (handleConfirmText) => [
  {
    name: 'ID',
    minWidth: '320px',
    selector: 'id',
    sortable: true,
    cell: (row) => row.quiz_id
  },
  {
    name: 'Name',
    minWidth: '320px',
    selector: 'email',
    sortable: true,
    cell: (row) => row.quiz_name
  },
  {
    name: 'No of players',
    minWidth: '320px',
    selector: 'email',
    sortable: true,
    cell: (row) => row.quiz_name
  },
  {
    name: 'No of Quizes',
    minWidth: '320px',
    selector: 'email',
    sortable: true,
    cell: (row) => row.quiz_name
  },
  {
    name: 'Status',
    minWidth: '138px',
    selector: 'status',
    sortable: true,
    cell: (row) => (
      <Badge
        className="text-capitalize"
        color={statusObj[row.quiz_status]}
        pill
      >
        {row.quiz_status}
      </Badge>
    )
  },
  {
    name: 'Action',
    minWidth: '130px',
    selector: 'action',
    cell: (row) => (
      <div className="column-action">
        <a href={`/quiz/edit/${row.quiz_id}`}>
          <Edit width="17" className="cursor-pointer" />
        </a>
        <Trash2
          width="17"
          className="ml-2 cursor-pointer"
          onClick={() => handleConfirmText(row.quiz_id)}
        />
      </div>
    )
  }
]
